// password 
// 1. 6~20  
// 2.  1      
export const expPassword = /^(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9])(?=.*[0-9]).{8,16}$/

//  
// .   
export const expPhone = /[^0-9]/g

